<template>
  <div id="main" :class="dia ? 'dia' : 'noche'">
    <div class="container my-2">
      <h1 class="title text-center">Clima en</h1>
      <form class="buscar-lugar" v-on:submit.prevent="getClima">
        <input
          type="text"
          class="form-control text-muted form-rounded p-4 shadow-sm"
          placeholder="Que ciudad?"
          autocomplete="off"
          v-model="ciudadBuscar"
        />
      </form>
      <div icon="sunny">
        <span class="sun"></span>
      </div>
      <div class="card rounded my-3 shadow-lg back-card overflow-hidden">
        <div class="card-top text-center">
          <div class="nombre-ciudad my-3">
            <p>{{ clima.nombreCiudad }}</p>
            <span>...</span>
            <p class="">{{ clima.pais }}</p>
          </div>
        </div>
        <div class="card-body">
          <div class="card-mid">
            <div class="row">
              <div class="col-12 text-center temp">
                <span>{{ clima.temperatura }}&deg;C</span>
                <p class="my-4">{{ clima.descripcion }}</p>
              </div>
            </div>
            <div class="row">
              <div class="col d-flex justify-content-between px-5 mx-5">
                <p>
                  <img src="img/down.svg" alt="" />
                  {{ clima.tempBaja }}&deg;C
                </p>
                <p>
                  <img src="img/up.svg" alt="" />
                  {{ clima.tempAlta }}&deg;C
                </p>
              </div>
            </div>
          </div>
          <div class="card-bottom px-5 py-4 row">
            <div class="col text-center">
              <p>{{ clima.feelsLike }}&deg;C</p>
              <span>Se siente Bien</span>
            </div>
            <div class="col text-center">
              <p>{{ clima.humedad }}%</p>
              <span>Humedad</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dia:true,
      ciudadBuscar: "",
      clima: {
        nombreCiudad: "london",
        pais: "UK",
        temperatura: 12,
        descripcion: "Muchas Nubes",
        tempBaja: "8",
        tempAlta: "19",
        feelsLike: "20",
        humedad: "70",
      },
    };
  },
  methods: {
    getClima: async function () {
      console.log(this.ciudadBuscar);
      const APPI_KEY = "c6f6c8cacc4c4b6d31edf9d80cd34c25";
      const URL = `https://api.openweathermap.org/data/2.5/weather?q=${this.ciudadBuscar}&appid=${APPI_KEY}&units=metric`;

      const response = await fetch(URL);
      const data = await response.json();
      console.log(data);
      //
      this.ciudadBuscar = "";
      this.clima.nombreCiudad = data.name;
      this.clima.pais = data.sys.country;
      this.clima.temperatura = Math.round(data.main.temp);
      this.clima.descripcion = data.weather[0].description;
      this.clima.tempBaja = Math.round(data.main.temp_min);
      this.clima.tempAlta = Math.round(data.main.temp_max);
      this.clima.feelsLike = Math.round(data.main.feels_like);
      this.clima.humedad = Math.round(data.main.humidity);

      const tiempoDeDia = data.weather[0].icon;

      //Chekeando que sea de dia o de noche
      if(tiempoDeDia.includes("n")){
        this.dia = false;
      }else{
        this.dia = true;
      }
    },
  },
};
</script>

